<template>
  <el-container id="loginimg">
    <el-aside width="200px">


    </el-aside>
    <el-container>
      <el-header>


      </el-header>
      <el-main >
        <!--登录的界面组件-->
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm"  class="demo-ruleForm" id="acssss" style=" box-shadow: 12px 12px 12px 12px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04)" >
          <el-form-item label="账号" prop="account" >
            <el-input v-model="ruleForm.account" placeholder="请输入账号" ></el-input>
          </el-form-item>
          <el-form-item label="密码" prop="password">
            <el-input v-model="ruleForm.password" placeholder="请输入密码" show-password ></el-input>
          </el-form-item>
          <el-form-item label="七天免密登录" prop="password">
            <el-switch
                v-model="ruleForm.value"
                active-color="#13ce66"
                inactive-color="#ff4949">
            </el-switch>
          </el-form-item>
          <el-form-item>
            <el-button type="primary"  @click="submitForm('ruleForm')" round >登录</el-button>
            <el-button type="success" @click="dialogFormVisible = true" round>注册</el-button>
            <br> <br>
          </el-form-item>
        </el-form>

        <!--注册弹出的-->
        <el-dialog title="注册账号信息" :visible.sync="dialogFormVisible">
          <el-form :model="form">
            <el-form-item label="账号" :label-width="formLabelWidth">
              <el-input v-model="form.account" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="密码" :label-width="formLabelWidth">
              <el-input v-model="form.password" autocomplete="off"></el-input>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button @click="dialogFormVisible = false">取 消</el-button>
            <el-button type="primary" @click="AddUserData">确 定</el-button>
          </div>
        </el-dialog>
      </el-main>
      <el-footer>


      </el-footer>
    </el-container>
  </el-container>
</template>

<script>
import axios from "axios";
export default {
  name: 'LoginVi',
  components: {},
 data(){
   return {
     ruleForm: {
       account: '',
       password:'',
       //remember:true,
       value:false
     },
     dialogFormVisible: false,
     form: {
       account: '',
       password: ''
     },
     formLabelWidth: '120px'
   };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          axios.get("http://93rf36tz6467.vicp.fun:15427/login?account="+this.ruleForm.account+"&password="+this.ruleForm.password).then(res => {
            var vvv = res.data;
            if (vvv){
              if (this.ruleForm.value){
                if (this.$cookies.get("account") == undefined || this.$cookies.get("password") == undefined ||
                    this.$cookies.get("account") != this.ruleForm.account || this.$cookies.get("password") != this.ruleForm.password){
                  console.log("666666666666666666")
                  this.$cookies.set("account", this.ruleForm.account,3600*24*7);
                  this.$cookies.set("password", this.ruleForm.password,3600*24*7);
                }
              }
              window.sessionStorage["account"]=this.ruleForm.account;
              window.sessionStorage["password"]=this.ruleForm.password;
              this.$message({
                message: '登录成功,欢迎你 <'+this.ruleForm.account+'>',
                type: 'success'
              });
              this.$router.push('/NavMenuView');
            }else {
              this.$message.error('账号密码错误,请重新输入');
            }
          })
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    AddUserData() {
      axios.get("http://93rf36tz6467.vicp.fun:15427/enroll?account="+this.form.account+"&password="+this.form.password).then(res => {
        var data = res.data;
        if (data){
          alert("注册成功")
        }else {
          alert("注册失败")
        }
      })
      this.dialogFormVisible = false
    }
  },
  mounted() {
    this.ruleForm.account=this.$cookies.get('account');
    this.ruleForm.password=this.$cookies.get("password");
  }
}
</script>

<style >

#acssss{
    //text-align: center;
    line-height: 160px;
    width: 200px;
    margin: 0 auto;

}
#loginimg{
  background-image: url('http://93rf36tz6467.vicp.fun:15427/login/login.jpg');
  background-size: cover;
  //background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
}
</style>